html {
    box-sizing:border-box;
    height:100%;
}

* {
    box-sizing:border-box;
}

body {
    margin:0;
    background-color:#242424;
    padding-top:75px;
    position:relative;
    height:auto;
    min-height:100%;
    color:#fff;
}

header {
    min-height:300px;
    color:#fff;
    display:flex;
    flex-direction:column;
    justify-content:center;
    position:relative;
}

.social_link {
    text-decoration:none;
    display:inline-block;
    vertical-align:middle;
    transition:opacity .1s ease-in-out;
}

.social_link:hover:not(:focus) {
    opacity:.75;
}

header a svg {
    width:20px;
}

section {
    padding:30px;
    max-width:100%;
    width:640px;
    margin:0 auto;
    text-align:left;
    font-family: 'Montserrat', sans-serif;
    z-index:10;
    color:#fff;
}

i {
    font-size:24px;
    color:#fff;
    transition: opacity .2s ease-in-out;
}



h1 {
    margin:0 0 0 -7px;
    font-weight:700;
    font-size:64px;
    line-height:1em;
    letter-spacing:-3px;
    z-index:10;
    text-shadow: 0 4px 3px rgba(0,0,0,0.4),
    0 8px 13px rgba(0,0,0,0.1),
    0 18px 23px rgba(0,0,0,0.1);
}
h6 {
    margin:0 0 20px;
    font-weight:300;
    font-size:16px;
    z-index:10;
}

p {
    font-size:16px;
    margin:0;
    font-weight:300;
    z-index:10;
}

hr {
    z-index:10;
}

.box_relative {
    position:relative;
    display:inline-block;
    min-height:300px;
    width:100%;
    max-width:100%;
}

img {
    max-width:100%;
    margin:0 auto;
}

.background_polygon {
    position:absolute;
    top:0;
    left:0;
    bottom:0;
    right:0;
    transform:skewY(-4deg) scaleY(1.5);
    overflow:hidden;
}

.background_polygon.reverse {
    transform:skewY(4deg) rotateY(180deg) scaleY(1.5);
}

.background_polygon .child_polygon {
    z-index:11;
    animation-timing-function: cubic-bezier(0.42, 1, 0.58, 3);
    animation-iteration-count: infinite;
    animation-duration: 60s;
}

.background_polygon.reverse .child_polygon {
    will-change:transform;
    animation-delay:-30s;
}

.background_polygon .child_polygon:nth-child(1) {
    transform:scale(.33, .2) translate(-100%, 200%);
    filter:brightness(95%);
    animation-name: polygonOne;
}

.background_polygon .child_polygon:nth-child(2) {
    transform:scale(.33, .2) translate(100%, 200%);
    filter:brightness(85%);
    animation-name: polygonTwo;
}

.background_polygon .child_polygon:nth-child(3) {
    transform:scale(.2) translate(-200%, -200%);
    filter:brightness(65%);
    animation-name: polygonThree;
}

.background_polygon .child_polygon:nth-child(4) {
    transform:scale(.2) translate(-100%, -200%);
    filter:brightness(75%);
    animation-name: polygonFour;
}

.background_polygon .child_polygon:nth-child(5) {
    transform:scale(.2) translate(0, -200%);
    filter:brightness(85%);
    animation-name: polygonFive;
}

@keyframes polygonOne {
    0% {
        transform:scale(1, .2) translate(-80%, 20%) rotate(30deg);
    }

    25% {
        transform:scale(1.15, 0.7) translate(-70%, 20%) rotate(85deg);
    }

    50% {
        transform:scale(0.65) translate(-10%, 10%) rotate(285deg);
    }

    75% {
        transform:scale(0.35) translate(-40%, 80%) rotate(25deg);
    }

    100% {
        transform:scale(1, .2) translate(-80%, 20%) rotate(30deg);
    }
}

@keyframes polygonTwo {
    0% {
        transform:scale(.33, 0.8) translate(30%, 50%);
    }

    25% {
        transform:scale(1) translate(100%, -10%) rotate(-75deg);
    }

    50% {
        transform:scale(1.15) translate(50%, -40%) rotate(-125deg);
    }

    75% {
        transform:scale(0.75, 0.3) translate(75%, 100%) rotate(-50deg);
    }

    100% {
        transform:scale(.33, 1.2) translate(30%, 50%);
    }
}

@keyframes polygonThree {
    0% {
        transform:scale(0.5, 1.4) translate(-80%, 100%) rotate(-35deg);
    }

    25% {
        transform:scale(1.25, 1) translate(-60%, -30%) rotate(135deg);
    }

    50% {
        transform:scale(0.6, 1.3) translate(-30%, 10%) rotate(85deg);
    }

    75% {
        transform:scale(0.4, 0.8) translate(-130%, -50%) rotate(5deg);
    }

    100% {
        transform:scale(0.5, 1.4) translate(-80%, 100%) rotate(-35deg);
    }
}

@keyframes polygonFour {
    0% {
        transform:scale(1) translate(-100%, -120%) rotate(15deg);
    }

    25% {
        transform:scale(1.5) translate(-20%, -40%) rotate(-85deg);
    }

    50% {
        transform:scale(1.25) translate(30%, -10%) rotate(-25deg);
    }

    75% {
        transform:scale(0.6) translate(-30%, -90%);
    }

    100% {
        transform:scale(1) translate(-100%, -120%) rotate(15deg);
    }
}

@keyframes polygonFive {
    0% {
        transform:scale(1.5, 0.8) translate(0, -130%) rotate(-110deg);
    }

    25% {
        transform:scale(.95) translate(70%, 0) rotate(-40deg);
    }

    50% {
        transform:scale(.85) translate(50%, -30%) rotate(60deg);
    }

    75% {
        transform:scale(0.2, 0.4) translate(10%, -130%) rotate(20deg);
    }

    100% {
        transform:scale(1.5, 0.8) translate(0, -130%) rotate(-110deg);
    }
}

.purple_polygon {
    box-shadow:0 0 2px #330099;
}
.purple_polygon, .purple_polygon > .child_polygon {
    background-color:#330099;
    z-index:1;
}

.green_polygon {
    box-shadow:0 0 2px #009966;
}
.green_polygon, .green_polygon > .child_polygon {
    background-color:#009966;
    z-index:1;
}

.orange_polygon {
    box-shadow:0 0 2px #cc6600;
}
.orange_polygon, .orange_polygon > .child_polygon {
    background-color:#cc6600;
    z-index:1;
}

.card {
    background-color:#2d2d2d;
    box-shadow:0 2px 4px -1px rgba(0,0,0,0.2), 0 4px 5px 0 rgba(0,0,0,0.14), 0 1px 10px 0 rgba(0,0,0,0.12);
    border-radius:2px;
    margin:0 auto;
    text-align:center;
    position:relative;
    z-index:20;
}

.card p, .card ul {
    line-height:1.5em;
    margin-bottom:15px;
    text-align:left;
}

.card p:last-child, .card ul:last-child {
    margin-bottom:0;
}

.card p a {
    color:#fff;
    transition: opacity .2s ease-in-out;
    cursor:pointer;
}
.card p a:hover {
    opacity:.8;
}

.project_logo {
    max-width:240px;
}

.pad {
    padding:15px;
}

.image-slide-wrap {
    position:relative;
    height:0;
    padding-bottom:56.25%;
}

[data-image-slide] {
    position:absolute;
    top:0;
    bottom:0;
    left:0;
    right:0;
    opacity:0;
    visibility:hidden;
    transform:scale(0);
    transition:opacity .2s ease-in-out, visibility .2s ease-in-out, transform .2s ease-in-out;
}

[data-image-slide].active {
    opacity:1;
    visibility:visible;
    transform:scale(1);
}

.image-list {
    list-style-type:none;
    margin:0;
    padding:0;
    display:flex;
    flex-direction:row;
    flex-wrap:wrap;
}

.image-list li {
    padding:5px;
    cursor:pointer;
    transition:opacity .2s ease-in-out;
    display:flex;
    flex-direction:column;
    flex-basis:50%;
    max-width:50%;
}

.image-list li img {
    pointer-events:none;
}

.image_dialog {
    position:fixed;
    top:0;
    left:0;
    bottom:0;
    right:0;
    z-index:100;
    flex-direction:column;
    align-items:center;
    justify-content:center;
    padding:30px;
    display:none;
    transition:opacity .2s ease-in-out;
    transform:translateX(0);
}

.image_dialog.active {
    display:flex;
    animation-name:dialogFadeIn;
    animation-iteration-count:1;
    animation-duration:.4s;
}

@keyframes dialogFadeIn {
    from {
        opacity:0;
    }

    to {
        opacity:1;
    }
}

.dialog_background {
    position:absolute;
    top:0;
    left:0;
    bottom:0;
    right:0;
    z-index:101;
    background:rgba(0,0,0,.6);
}

.dialog_foreground {
    background-color:#2d2d2d;
    box-shadow:0 2px 4px rgba(0,0,0,.8);
    position:relative;
    z-index:102;
    width:100%;
    max-width:1920px;
    height:0;
    padding-bottom:49.55%;
}

.dialog_button {
    position:absolute;
    height:50px;
    width:50px;
    font-size:32px;
    background-color:#2d2d2d;
    color:#fff;
    border:none;
    outline:none;
    box-shadow:0 2px 4px rgba(0,0,0,.8);
    margin-top:-25px;
    font-family: 'Montserrat', sans-serif;
    transition:transform .1s;
    transform:scale(1);
    z-index:10;
}

.dialog_button:active {
    transform:scale(0.9);
}

.close_dialog {
    position:fixed;
    top:10px;
    right:10px;
    margin-top:0;
    font-size:24px;
    line-height:24px;
}

.previous_image {
    top:50%;
    left:-20px;
}

.next_image {
    top:50%;
    right:-20px;
}

.image_slide {
    animation-duration:.6s;
    animation-timing-function:cubic-bezier(0.25, 0.1, 0.25, 1.0);
    animation-iteration-count: 1;
    position:absolute;
    height:auto;
    top:0;
    left:0;
    z-index:1;
    opacity:0;
}

.image_slide.active {
    z-index:2;
    opacity:1;
}

.enter_right {
    animation-name:enterRight;
}
.exit_right {
    animation-name:exitRight;
}
.enter_left {
    animation-name:enterLeft;
}
.exit_left {
    animation-name:exitLeft;
}

@keyframes enterRight {
    from {
        opacity:0;
        transform:translateX(-100%);
    }

    to {
        opacity:1;
        transform:translateX(0);
    }
}

@keyframes exitRight {
    from {
        opacity:1;
        transform:translateX(0);
    }

    to {
        opacity:0;
        transform:translateX(100%);
    }
}

@keyframes enterLeft {
    from {
        opacity:0;
        transform:translateX(100%);
    }

    to {
        opacity:1;
        transform:translateX(0);
    }
}

@keyframes exitLeft {
    from {
        opacity:1;
        transform:translateX(0);
    }

    to {
        opacity:0;
        transform:translateX(-100%);
    }
}


@media only screen and (min-width: 768px) {
    h1 {
        font-size:72px;
    }

    .show_mobile_only {
        display:none;
    }
}

